import React, { Component } from 'react';
import { CardContent, Grid, Card, Divider, IconButton, Avatar } from '@mui/material';
import AddIcon from '@mui/icons-material/Add';
import RemoveIcon from '@mui/icons-material/Remove';

class Country extends Component {
    state = { 
        country: 'United States',
        gold: 0,
        silver: 0,
        bronze: 0,
     } 
     handleIncrementGold = () => {
         this.setState({gold: this.state.gold +1});
     }
     handleDecrementGold = () => {
         if(this.state.gold > 0) {
            this.setState({gold: this.state.gold -1});
         }
     }
     handleIncrementSilver = () => {
        this.setState({silver: this.state.silver +1});
    }
    handleDecrementSilver = () => {
        if(this.state.silver > 0) {
           this.setState({silver: this.state.silver -1});
        }
    }
    handleIncrementBronze = () => {
        this.setState({bronze: this.state.bronze +1});
    }
    handleDecrementBronze = () => {
        if(this.state.bronze > 0) {
           this.setState({bronze: this.state.bronze -1});
        }
    }
    render() { 
        return (
            <Grid container spacing={2}>
                <Card className='Country-Card' sx={{ minWidth: 350 }}>
                    <CardContent>
                        <Grid container spacing={2}>
                            <Grid item xs={12}>
                                <h1>{ this.state.country }</h1>
                                <Divider variant="fullWidth" />
                            </Grid>
                            {/* Gold Row */}
                            <Grid item xs={2} className='Second-Row'>
                                <Avatar sx={{bgcolor: '#FFD700'}}>{ this.state.gold }</Avatar>
                            </Grid>
                            <Grid item xs={6} className='Second-Row'>Gold Medals</Grid>
                            <Grid item xs={2}>
                                <IconButton color="primary" onClick={ this.handleIncrementGold }>
                                    <AddIcon />
                                </IconButton>
                            </Grid>
                            <Grid item xs={2}>
                                <IconButton color="primary" onClick={ this.handleDecrementGold }>
                                    <RemoveIcon />
                                </IconButton>
                            </Grid>

                            {/* Silver Row */}
                            <Grid item xs={2} className='Third-Row'>
                                <Avatar sx={{bgcolor: '#C0C0C0'}}>{ this.state.silver }</Avatar>
                            </Grid>
                            <Grid item xs={6} className='Third-Row'>Silver Medals</Grid>
                            <Grid item xs={2}>
                                <IconButton color="primary" onClick={ this.handleIncrementSilver }>
                                    <AddIcon />
                                </IconButton>
                            </Grid>
                            <Grid item xs={2}>
                                <IconButton color="primary" onClick={ this.handleDecrementSilver }>
                                    <RemoveIcon />
                                </IconButton>
                            </Grid>

                            {/* Bronze Row */}
                            <Grid item xs={2} className='Fourth-Row'>
                                <Avatar sx={{bgcolor: '#CD7F32'}}>{ this.state.bronze }</Avatar>
                            </Grid>
                            <Grid item xs={6} className='Fourth-Row'>Bronze Medals</Grid>
                            <Grid item xs={2}>
                                <IconButton color="primary" onClick={ this.handleIncrementBronze }>
                                    <AddIcon />
                                </IconButton>
                            </Grid>
                            <Grid item xs={2}>
                                <IconButton color="primary" onClick={ this.handleDecrementBronze }>
                                    <RemoveIcon />
                                </IconButton>
                            </Grid>
                        </Grid>
                    </CardContent>
                </Card>
            </Grid>
        );
    }
}
 
export default Country;